
.pagination {
  float:right;
  padding-right:10px;  
}
.pagination ul {
  list-style-type:none;
}
.pagination ul li {
  display:inline-block;
  background:#efefef;
  border-radius:12px;
  height:24px;
  width:24px;
  text-align:center;
  margin-right:2px;
}
.pagination ul li:hover{
  background:#2bbb9c;
}
.pagination ul li a {
  text-decoration:none;
  display:block;
  width:24px;
  height:24px;
  margin:0 auto;
  padding-top:5px;
  font-size:14px;
  color:#999;
}
.pagination ul li a:hover{
  color:#fff;
}
.pagination .active {
  background:#2bbb9c;  
}
.pagination .active  a{
  color:#fff;
}
.pagination .disabled{
  background:#fff;
  cursor:default;
  color: #fff !important;
}
.pagination>ul {
  float:right;
}
.pagination .stastics {
  float:right;
  font-size: 12px;
  padding-top: 5px;
  margin-right: 5px;
  margin-top: 16px;
}
.pagination .stastics span{
  color:#2bbb9c;
}



