
  .vue-grid {
    width:980px;
  }
  .table {
    padding-bottom: 10px;
		width:100%;
  }
	.table table {
		width:100%;
		font-size:12px;
    text-align: left;
	}
	.table thead tr{
		background: #eaf3f6;
		border-bottom:2px solid #fff;
	}
  .table thead th {
		font-size: 12px;
    font-weight: bold;
    line-height: 31px;
    padding-left: 5px;
    color: #085982;
		cursor:pointer;
  }
	.table tbody tr {
		background: #f5f8f9;
		line-height: 2.6em;
		margin-bottom:2px;
		border-bottom: 2px solid #fff;
	}
	.table tbody tr:hover {
		background:#eaf3f6;
	}
	.table tbody tr td{
		padding-left:2px;
	}
	.arrow.asc {
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-bottom: 4px solid #fff;
	}
	.arrow.dsc {
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-top: 4px solid #fff;
	}
  .operation{
    height:30px;
    margin-top:5px;
    margin-bottom:3px;
  }
  .search{
    float:right;
  }	
  .search input{
    height:25px;
  }
  .table .operation-th {
    width:30px;
  }	



